<template>
  <v-container>
    <v-row justify-sm-xs="center" >
      <v-flex md12 xs12 sm12 >
        <h4 class="display-1">Créer un Meetup</h4>
        <v-form @submit.prevent >
            <v-layout  justify-center row wrap style="padding: 0 30px">
              <v-flex md6 xs12 sm12  >
              <v-text-field  v-model="meetup.titre"
                label="Titre"
                required
                color="purple draken-1"
              ></v-text-field>
              <v-text-field v-model="meetup.lieu"
                label="Lieu"
                required
                color="purple draken-1"
              ></v-text-field>
              <v-row >
                <v-col style="text-align: left">
                  <v-btn @click="onPickImage" raised >Votre image</v-btn>
                    <input @change="onFilePicked" type="file" style="display: none" ref="fileInput" accept="image/*" >
                  </v-col>
              </v-row>
              <div>
                <img class="image" :src= " meetup.imageU">
              </div>
                <v-textarea
                label="Description" v-model="meetup.description"
                filled
                validate-on-blur
                rows="1"
                color="purple draken-1"
                ></v-textarea>
                </v-flex>
                    <v-flex md3 sm10 xs10 class="mt-1" >
                            <h4>Choisisez une date</h4>
                          <v-date-picker  @change="getDate" reactive width="220"  locale="fr" color="purple lighten-1"
                           v-model="meetup.date"></v-date-picker>
                      </v-flex>
                      <v-flex md3 sm10 xs10 class="mt-1" >
                        <h4>Choisisez un horraire</h4>
                        <v-time-picker @change="getTime" width="220" format="24hr" v-model="meetup.time" 
                        color="purple lighten-1"></v-time-picker>
                      
                         <v-btn @click="createMeet" color="purple lighten-1" style="margin-top:30px" :disabled="!formIsValid" 
                        class="white--text" rounded>Valider</v-btn>
                      </v-flex>     
            </v-layout>
        </v-form>
      </v-flex>
    </v-row>
  </v-container>
</template>

<script>
export default {

  data(){

    return {
      
      meetup: {
        titre:'',
        lieu:'',
        description:'',
        time:"",
        date:"",
        imageU:'',
        image:null,
        createdId: Date.now(),
      },
     
    }

  },
  computed:{

    formIsValid(){ return this.meetup.titre !== "" && this.meetup.lieu !== '' && this.meetup.imageU !==""
     && this.meetup.date && this.meetup.time}
  },

  
  methods:{

    createMeet(){
      
      this.$store.dispatch('addMeetup',this.meetup);
    
    },
    
    getTime(e){ return this.meetup.time === e},

    getDate(e){return this.meetup.date === e},

    onPickImage(){ this.$refs.fileInput.click()},

    onFilePicked(e){

      const files = e.target.files
      let fileName = files[0].name
      if(fileName.lastIndexOf('jpg') == -1 && fileName.lastIndexOf('png') == -1 
      && fileName.lastIndexOf('JPG') == -1 && fileName.lastIndexOf('PNG') == -1){ return alert('Fichier non valide !'); }

      const filereader = new FileReader()

      filereader.readAsDataURL(files[0])
      filereader.addEventListener('load', () => { this.meetup.imageU = filereader.result })

      this.meetup.image = files[0]
      
    }

  }
}
</script>

<style scoped>

h4{ color:#6A1B9A}

.image {

  max-width: 50%;
  max-height: 170px;
  margin-right: 370px;
}


</style>