<template>
 <v-container>
   <v-row justify="center">
         <v-col md="1" sm="12" xs="12" cols="12">
          <div v-if="loading" style="margin-top: 100px">
           <loader/>
         </div>
         <div v-show="meetups.length < 1 && !loading">
         <alertNoMeet/>
         </div>
         </v-col>
   </v-row>
   <v-row v-show="meetups.length >= 1 && !loading">
     <v-col md="3" sm="10"  xs="10" cols="12" v-for="(item,i) in meetups"
          :key="i">
       <div>
        <v-card  class="mt-2">
          <v-img 
            class="white--text align-end"
            height= '30vh'
            :src= "item.imageU"
          >
            <v-card-title>{{item.titre}}</v-card-title>
          </v-img>
            <h4 class="mb-5 mt-1">{{item.description}}</h4>
            <p style="font-weight: 500" > <span>Lieu : </span> {{item.lieu}}</p>
            <p style="font-weight: 500"><span>Date :</span> {{item.date | date}}</p>
            <p style="font-weight: 500" ><span>Horaire :</span> {{item.time}}</p> 
          <v-card-actions>
            
            <v-btn @click="toView(item.id)"
              color="#6A1B9A"  
              text
            >
              Voir meetup
            </v-btn>
          </v-card-actions>
        </v-card>
       </div>
        </v-col>
   </v-row>
 </v-container>
</template>
<script>

import {mapGetters} from 'vuex'
import alertNoMeet from '@/components/alertNoMeet'
import loader from '@/components/loader'

export default {

  components:{alertNoMeet,loader},

  computed : {
    
    ...mapGetters(['meetups','loading'])
    
  },

  methods: {

    toView(id){this.$router.push('viewMeet/'+ id)}
  }

}
</script>

<style>

.text {

  height: 150px;
  text-align: left;
}


</style>