<template>
  <v-row justify="center" >
      <v-col  >
    <v-dialog v-model="showDialog" width="350px" class="mb-15">
        <v-form @submit.prevent="confirmUpdate(fieldMessage)">
            <v-card>
                <v-card-title class="purple ligthen-4 white--text">Confirmez vous le changement d'{{fieldMessage}} ?</v-card-title>
            <v-card-actions style="margin-left: 90px">
                <v-btn color="purple draken-1" text type="submit">Oui</v-btn>
                <v-btn color="purple draken-1" @click="close" text>Non</v-btn>
            </v-card-actions>
            </v-card>
        </v-form>
    </v-dialog>
      </v-col>
  </v-row>
</template>
<script>    

export default {

    props: ['showDialog','fieldMessage'],

    methods:{

        confirmUpdate(field){ 

            if(field === "avatar"){

                this.$emit('closeConfirm');
                this.$emit('updateAvatar');

            } else{
                
                this.$emit('closeConfirm')
                this.$emit('updateUserProfil')
            }

        },

        close(){

            this.$emit('closeConfirm')
            this.$emit('cancelChange')
        },

        
    }


}
</script>

