<template>
<div>
  <v-container style="margin-top:-50px"> 
  <v-row justify="center" >
    <v-col cols="12" md="12" sm="12">
      <v-row justify="center" align="center">
        <v-col cols="12" md="1" sm="10" xs="10">
          <loader v-show="sharedMeetup == ''"/>
          <formParticipants @close="toggleForm" :showForm="showForm" @addParticipant="addParticipant($event)"/>
        </v-col>
      </v-row> 
          <v-row justify="space-around">
            <v-col md="6" sm="12" xs="12" cols="12">
              <v-card v-show="sharedMeetup !==''"
                >
                <v-img 
                class="white--text align-end"
                height="250px"
                :src= "sharedMeetup.imageU" >
                  <v-card-title>{{sharedMeetup.titre}}</v-card-title>
                </v-img>
                  <v-card-text style="height:215px">
                    <h4 class="mb-3">{{sharedMeetup.description}}</h4>
                    <p style="font-weight:600"><span>Date : </span>{{sharedMeetup.date | date}}</p>
                    <p style="font-weight:600" ><span>heure : </span>{{sharedMeetup.time}}</p>
                    <p style="font-weight:600" ><span>Lieu : </span>{{sharedMeetup.lieu}}</p>
                    <v-row>
                    <v-card-subtitle style="text-align:left" >Meetup organisé par <span>{{sharedMeetup.username}}</span></v-card-subtitle>
                      <v-spacer></v-spacer> 
                      <v-btn small @click="toggleForm"  color="purple lighten-2 white--text" class="mt-3" 
                      style="margin-right:10px" rounded>Participé au Meet ?</v-btn>
                    </v-row>
                  </v-card-text>
              </v-card>
            </v-col>
            <v-col class="mt-10" md="3" sm="12" xs="12" v-show="sharedMeetup !==''">
              <participants :sharedMeetup="sharedMeetup"/>
            </v-col>
      </v-row>
    </v-col>
  </v-row>
  </v-container>
</div>
</template>
<script>

import * as firebase from '../../Firebase/firebase'
import loader from '@/components/loader'
import participants from '@/components/participants'
import formParticipants from '@/components/formParticipants'

export default {

  mounted: async function(){

    const url = window.location.href

    let index = url.indexOf('meetUrl/')

    let urlId = url.slice(index + 8)

    try {

      const meetup = await firebase.db.collection('meetups').doc(urlId).get()
      this.sharedMeetup = meetup.data()
      
    } catch (error) { console.log(error)}
 
  },

  components:{loader,participants,formParticipants },

 data(){

  return{

    sharedMeetup:'',
    showForm:false,

  }
},

  methods:{

    toggleForm(){ this.showForm = !this.showForm},

  async addParticipant(invite){

    this.toggleForm()
      
    this.sharedMeetup.participants.push(invite)

    try {

      await firebase.db.collection('meetups').doc(this.sharedMeetup.id).update(
      {participants: this.sharedMeetup.participants})
      
    } catch (error) { console.log(error)}

  }
  }
}

</script>

<style>

.wrapText{

  padding:  10px;
}

</style>