<template>
<v-container>
  <v-row justify="center">
    <v-col cols="12" md="6">
      <h4>Créer votre compte via facebook</h4>
      <v-btn  class="mb-5" @click="signUpWithFacebook" x-large icon><v-icon x-large color="#3b5998" >fab fa-facebook-square</v-icon></v-btn>
      <div style="border-bottom: 2px solid violet"></div>
      <h4 class="mt-10" >Ou</h4>
      <h4>Creez votre compte</h4>
      <v-form @submit.prevent="signUpUser">
        <v-text-field  color="purple draken-3" type="text" @input ="clearErr" v-model.trim =" registUser.username" label="Username" required id="username" name="username" clearable ></v-text-field>
        <span v-show="validateUser" v-text="usernameAlert" style= "color: #6A1B9A" ></span>
        <v-text-field   color="purple draken-3" @input ="clearErr" v-model.trim  ="registUser.email" label="email" type="email" id="email" name="email"  clearable ></v-text-field>
        <span v-show="validateEmail" v-text="emailAlert" style= "color: #6A1B9A" ></span>
        <v-text-field  color="purple draken-3" @input ="clearErr"  v-model.trim ="registUser.password" type="password" label="Password" id="password" name="password"
        required  hint="At least 6 characters" clearable ></v-text-field>
        <span v-show="validatePassword" v-text="passwordAlert" style= "color: #6A1B9A" ></span>
        <v-row>
      <v-btn
        color="purple draken-1 white--text"
        class="mr-4"
        type="submit"
      >
        Valider
      </v-btn>
      </v-row>
      </v-form>
       <v-row justify-md="center">
         <v-col cols="12" md="6">
         </v-col>
      <v-col md="12"  style="margin-top: 20px">
       <alertError/>
      </v-col>
      </v-row>
    </v-col>
  </v-row>
</v-container>
</template>
<script>

import alertError from '@/components/alertError'
import {mapGetters} from 'vuex'

export default {

  components : { alertError},

  data(){

    return{

      registUser:{
        username: undefined,
        email:null,
        password:null,
      },
      usernameAlert :'username is required',
      emailAlert :'email is required',
      passwordAlert :'password is required',
      validateUser: false,
      validateEmail:false,
      validatePassword:false,
    }
  },  

  computed:{ 

    ...mapGetters(['logErrorMess']),
    
  },

  methods: {

    signUpUser(){ 

      
      if( this.registUser.username == undefined){ this.validateUser = true} 
      
      if (this.registUser.email == null ){ this.validateEmail = true;} 
      
      if (this.registUser.password == null ){ this.validatePassword = true;}

      if (!this.validateUser && !this.validateEmail  && !this.validatePassword ){

      this.$store.dispatch('signupUser', this.registUser);
      this.$store.commit('setClearError')

      } else { return}

     
    },

     signUpWithFacebook(){ 
      
      this.$store.dispatch('signUpUserFbook');
      this.$store.commit('setClearError');
    },

    clearErr(){   
      
     this.$store.commit('setClearError');

     if(this.validateUser == true){this.validateUser = false}
     if(this.validateEmail == true){this.validateEmail = false}
     if(this.validatePassword == true){this.validatePassword = false}
    },
  }

}
</script>

<style>

</style>