<template>
  <v-row justify="center">
    
      <v-dialog v-model="showSocialShare" width="270px">
        <v-card height="140px" >
          <v-card-title >Envoyer une invitation</v-card-title>
              <ShareNetwork
                v-for="network in networks"
                :network="network.network"
                :key="network.network"
                :url="sharing.url"
                :title="sharing.title"
                :description="sharing.description"
                :quote="sharing.quote"
              >
              <v-btn icon @click=" handleShare" ><v-icon :color="network.color" >{{network.icon}}</v-icon></v-btn>
              </ShareNetwork>
              <v-btn icon @click="sharee"><v-icon color="#1877f2">fab fa-facebook</v-icon></v-btn>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn small @click="$emit('close')" icon><v-icon color="purple lighten-1" >fas fa-window-close</v-icon></v-btn>
              </v-card-actions>
        </v-card>
      </v-dialog>
  </v-row>
</template> 

<script>

import {mapGetters} from 'vuex'

export default {

  created: async function(){  

   window.fbAsyncInit = await function(){
    window.FB.init({
      appId            : "348183069913760",
      xfbml            : true,
      version          : 'v8.0'
    });

    window.FB.AppEvents.logPageView();   
      
  };

},

  props:['showSocialShare'],

  computed:{ ...mapGetters(['currentMeetup'])},

    data(){

      return {

        networks: [
        {network:'email', icon:'fas fa-envelope',color: '#333333'},
        {network:'whatsapp', icon:'fab fa-whatsapp',color: '#25d366'},
        { network: 'viber', icon: 'fab fah fa-lg fa-viber', color: '#59267c' },
      ],

        sharing: {
        url: '',
        title:'',
        description: '',
        quote: '',
        hashtags: '',
      },
    }
  },

 methods:{

  handleShare(){ 


    this.sharing.url = 'https://raffortville.github.io/Vue-Meeting-Point/#/meetUrl/' + this.currentMeetup.id;
    this.sharing.description = this.currentMeetup.titre;
    this.sharing.quote =  this.currentMeetup.username + ' vous envoie une invitation' + '  ' + this.currentMeetup.titre
    this.sharing.title = this.currentMeetup.username + ' vous envoie une invitation' 


    this.$emit('close')
  },
 async sharee(){

 await window.FB.ui({
  method: 'share',
  href: 'https://raffortville.github.io/Vue-Meeting-Point/#/meetUrl/' + this.currentMeetup.id,
}, 

function(response){ if(response){console.log(response)}});

},
 },

}
</script>

