<template>
  <div class="home">
    <v-row v-show="loading" justify="center" style="margin-top:200px">
      <v-col md="1" cols="12" sm="1" xs="1">
    <loader/>
    </v-col>
    </v-row>
    <v-container v-show="!loading">
      <v-row justify="center">
        <v-col cols="12" md="4" sm="12" xs="12">
          <editProfilMess/>
        </v-col>
      </v-row>
      <v-layout row wrap justify-center> 
        <v-flex md4 xs12 menu v-show="checkMeetups">
          <v-btn to="/viewMeet"  rounded color="purple lighten-2  white--text" >Voir les meetups</v-btn>
        </v-flex>
        <v-flex md4 xs12 menu>
          <v-btn @click="toView"  rounded  color="purple lighten-2  white--text ">Créer un meetup</v-btn>
        </v-flex>
      </v-layout>
      <v-layout justify-center>
        <v-flex  md6 xs12 v-show="checkMeetups">
          <carousel/>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>
<script>

import carousel from '@/components/carousel'
import loader from '@/components/loader'
import editProfilMess from '@/components/editProfilMess'
import {mapGetters} from 'vuex'

export default {
  name: 'Home',

  components: {carousel, editProfilMess,loader},

  computed:{
    
    ...mapGetters(['meetups','loading']),

    checkMeetups(){ return this.meetups.length > 0 },
  
  },

  methods:{

    toView(){this.$router.push('createMeet')}
  }
  
}
</script>

<style  scoped>

.menu{

  padding: 20px 0px;
}

</style>
