import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import login from '../views/Profil/login.vue'
import settings from '../views/Profil/settings.vue'
import register from '../views/Profil/register.vue'
import viewMeet from '../views/Meet/viewMeet.vue'
import viewMeetId from '../views/Meet/viewMeetId.vue'
import createMeet from '../views/Meet/createMeet.vue'
import signout from '../views/Profil/signout.vue'
import meetUrl from '../views/Meet/meetUrl.vue'
import {auth} from '../Firebase/firebase'


Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta:{
      requiresAuth:true
    }

  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/register',
    name: 'register',
    component: register
  },
  {
    path: '/settings',
    name: 'settings',
    component: settings,
    meta:{
      requiresAuth:true
    }
    
  },
  {
    path: '/signout',
    name: 'signout',
    component: signout,
    meta:{
      requiresAuth:true
    }
  },
  {
    path: '/viewMeet',
    name: 'viewMeet',
    component: viewMeet,
    meta:{
      requiresAuth:true
    }
  },
  {
    path:'/viewMeet/:id',
    name:'viewMeetId',
    component: viewMeetId,
    meta:{
      requiresAuth:true,
    }
  },
  {
    path:'/meetUrl/:id',
    name:'meetUrl',
    component: meetUrl,
  
  },
  {
    path: '/createMeet',
    name: 'createMeet',
    component: createMeet,
    meta:{
      requiresAuth:true
    }
  },
]

const router = new VueRouter({
  routes,
  history,
  base: process.env.BASE_URL,
  
})

router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some(x => x.meta.requiresAuth)

  if (requiresAuth && !auth.currentUser){
    next('/login') 

  } else {

    next()
  }
})



export default router
