<template>
  <v-carousel  cycle
       height="350">
    <v-carousel-item style="cursor:pointer"
      v-for="(item,i) in meetups"
      :key="i"
      :src= "item.imageU"
      reverse-transition="fade-transition"
      transition="fade-transition"
      @click="onClickViewMeet(item.id)"
    >
    <div><h4 class="titre">{{item.titre}}</h4></div>
    </v-carousel-item>
  </v-carousel>
</template>

<script>

import { mapGetters } from 'vuex'

  export default {

    name: 'carousel',

    computed: { ...mapGetters(['meetups'])},

    methods: {

      onClickViewMeet(id){this.$router.push('/viewMeet/'+ id)},
    },
   
  }
</script>

<style  scoped>

.titre {

    color: white;
    background-color: rgba(0,0,0,0.3);
    letter-spacing: 2px;
    width: 50%;
    margin: 0 auto;
    font-weight: 400;
}

.v-window__container:hover{

  cursor: pointer;
}



</style>