<template>
  <div>
      <v-row justify="center" >
          <v-col cols="12" md="1" sm="12" xs="12">
            <v-dialog v-model="showForm" width="400px">
                <form @submit.prevent="handleParticipant">
                    <v-card>
                        <v-card-title>Participation au Meetup</v-card-title>
                        <v-card-text>
                             <v-text-field v-model="nom" color="purple draken-1" label="Votre Nom"></v-text-field>
                             <small v-show="nameField">Veuillez rentrer un nom</small>
                             <v-row justify="space-around">
                                 <v-radio-group v-model="radio">
                                     <v-radio color="purple draken-1" label="Oui"></v-radio>
                                     <v-radio color="purple draken-1" label="Non"></v-radio>
                                     <v-radio color="purple draken-1" label="Peut-être"></v-radio>
                                 </v-radio-group>
                             </v-row>
                        </v-card-text>
                        <v-card-actions>
                            <v-btn @click="closeForm" icon>
                                <v-icon color="purple lighten-1">fas fa-window-close</v-icon>
                            </v-btn>
                            <v-spacer></v-spacer>
                            <v-btn type="submit"  color="purple lighten-1" text>Valider</v-btn>
                        </v-card-actions>
                    </v-card>
                </form>
            </v-dialog>
          </v-col>
      </v-row>
  </div>
</template>

<script>
export default {

    props:['showForm'],

    methods:{

        handleParticipant(){

            switch(this.radio){

                case 0 :this.reponse = "Oui";
                break;
                case 1 :this.reponse = "Non";
                break;
                case  2 :this.reponse = "Peut-être";
                break;
            }

            if(this.nom === ''){ 
                
                this.nameField = true;
                return;

            } else {

                const invite = {nom:this.nom,reponse:this.reponse}
                this.$emit('addParticipant',invite)
            }
            
        },

        closeForm(){this.$emit('close')}
    },

    data(){

        return{

            radio:2,
            nom:'',
            reponse:'',
            nameField: false
        }
    }

}
</script>

