import * as firebase from '../../Firebase/firebase'
import fb from 'firebase/app'
import router from  '../../router'
import meetupStore from './meetupStore';


const state = {

  currentUser: {},
  logUser:false,
  logInError:{},
  editProfilSucces:''
    
};

 const mutations = {

  setCurrentUser(state,payload){ state.currentUser = payload;},

  setLogUserIn(state){state.logUser = true},

  setLogUserOut(state){state.logUser = false},

  setLogError(state,payload){state.logInError = payload},

  setClearError(state){state.logInError = {}},

  setEditProfilSucces(state,payload){state.editProfilSucces = payload},

  setEditProfilSuccesClear(state,payload){state.editProfilSucces = payload},

};

 const actions = {

    async signupUser({dispatch,commit},payload){

        const email = payload.email; const password = payload.password
  
        try {
  
          const {user} = await firebase.auth.createUserWithEmailAndPassword(email,password)
  
          payload.userId = await user.uid

          await firebase.db.collection('users').doc(user.uid).set(payload)

          dispatch('loadUser',user.uid)
  
          router.replace('/')
          
        } catch(error){console.log(error); commit('setLogError', error)}
    },

    async signUpUserFbook({commit,dispatch}){

      try {

        const result = await firebase.auth.signInWithPopup(firebase.provider)

        await firebase.db.collection('users').doc(result.user.uid).set({
          
          avatar:result.user.$.currentUser.photoURL,
          email:result.user.$.currentUser.email,
          userId:result.user.uid,
          username: result.user.$.currentUser.displayName,
          SignUpWithFacebook:true,
        })

        dispatch('loadUser', result.user.uid)
        router.push('/')

      } catch (error) {console.log(error);commit('setLogError',error)}
    },

    async loginUser({dispatch,commit},payload){

        try {
  
          const {user} = await firebase.auth.signInWithEmailAndPassword(payload.email,payload.password)

          dispatch('loadUser',user.uid)

          router.push('/')
      
        } catch (error) { 
          
          console.log(error);
          commit('setLogError', error)
        
        }
    },

    async loadUser({commit,dispatch},payload){

      try {

        const userLog = await firebase.db.collection('users').doc(payload).get()

        commit('setCurrentUser',userLog.data())
        commit('setLogUserIn')
        dispatch('fetchMeetups');
       
      } catch (error) {console.log(error)}
  
    },

    async updateUsername({dispatch,commit},payload){

      let succesMess = "Nom d'utilisateur changé avec succeès !"

      try {

        const user = await firebase.db.collection('users').doc(payload.userId)

        await user.update({username: payload.username})

        if(meetupStore.state.currentMeetup.id){
          
          await firebase.db.collection('meetups').doc(meetupStore.state.currentMeetup.id).update({username:payload.username})
        }

        if(payload.SignUpWithFacebook){

          dispatch('loadUser',payload.userId);
          router.push('/')

        } else {

          dispatch('loginUser',payload)
        }
      
       commit('setEditProfilSucces', succesMess)
        
      } catch (error) { console.log(error)}
    },

    async updateMail({dispatch,commit},payload){

      let succesMess = "Adresse mail changée avec succeès !"

      try {

        const user =  await firebase.auth.currentUser

        const cred =  await  fb.auth.EmailAuthProvider.credential(user.email,payload.password)

        await user.reauthenticateWithCredential(cred);

        await user.updateEmail(payload.email)

        const userLog = await firebase.db.collection('users').doc(payload.userId)

        await userLog.update({email: payload.email})

        dispatch('loginUser',payload)
        commit('setEditProfilSucces',succesMess )

      } catch (error){console.log(error)}
    },
       
    async signoutUser({commit}){

        try {
  
          await firebase.auth.signOut()
  
          commit('setCurrentUser',{})
          commit('setMeetups',[])
          commit('setLogUserOut')

          router.push('/login')
          
        } catch (error) { console.log(error)}
    },

 };

 const getters = {

  currentUser: state =>state.currentUser,
  logUser: state => state.logUser,
  logErrorMess: state => state.logInError,
  editProfilSuccesMess: state => state.editProfilSucces
};

 export default {
    state,
    getters,
    actions,
    mutations
 }