import * as firebase from '../../Firebase/firebase'
import {storage} from '../../Firebase/firebase'
import router from  '../../router'
import authStore from './authStore';


const state = {
    
  meetups: [],
  currentMeetup: {},
  loading: true,

};

 const actions = {

   async addMeetup({dispatch,commit},meetup){

    meetup.userId = firebase.auth.currentUser.uid
   
      commit('setLoading',true)

      meetup.username = authStore.state.currentUser.username
    
      try {   

        const response = await firebase.db.collection('meetups').add({
         
        titre: meetup.titre, date: meetup.date, lieu: meetup.lieu, time: meetup.time,username:meetup.username,
        createdId: meetup.createdId, userId: meetup.userId, description: meetup.description,participants:[]})

        router.push('/')

        await firebase.db.collection('meetups').doc(response.id).update({id:response.id})

        await storage.ref('meetups/' + response.id).put(meetup.image)

        const url = await storage.ref('meetups/' + response.id).getDownloadURL()

        await firebase.db.collection('meetups').doc(response.id).update({imageU:url})
       
       dispatch('fetchMeetups')
     
      
      } catch (error) { console.log(error)} 

   },

   async addAvatar({dispatch,commit},payload){

    const succesMess = 'Avatar changé avec succés !'

    try {

     await storage.ref('users/' + payload.userId).put(payload.avatarImg)

     const url = await storage.ref('users/' + payload.userId).getDownloadURL()

     await firebase.db.collection('users').doc(payload.userId).update({avatar:url})

     if(authStore.state.currentUser.SignUpWithFacebook){

      dispatch('loadUser',payload.userId);
      router.push('/')
    
     } else {dispatch('loginUser',payload);}

     commit('setEditProfilSucces', succesMess)

    } catch (error) { console.log(error)}
   },

   async fetchMeetups({commit},){

    try {

     const snapshot = await firebase.db.collection('meetups').where('userId', '==', firebase.auth.currentUser.uid).get()
     const meetupsArray = [];
    
     snapshot.forEach(doc => { 

      let meetup = doc.data()
      meetupsArray.push(meetup);
    })

     commit('setMeetups', meetupsArray)
    
     if (snapshot.empty) {  console.log('No matching documents.');
     
     commit('setLoading',false) 
     return;
    }

     commit('setLoading',false)
      
    } catch (error) {console.log(error)}
  },

  async deleteMeet({dispatch,commit},payload){

    commit('setLoading',true)

    await firebase.db.collection('meetups').doc(payload).delete()

    await storage.ref('meetups/'+ payload).delete()

    dispatch('fetchMeetups')
    commit('setCurrentMeetup',{})
    router.push('/viewMeet') 
  },

  async loadCurrentMeetup({commit},payload){

    try {

      const meet = await firebase.db.collection('meetups').doc(payload).get()

      commit('setCurrentMeetup',meet.data())
      
    } catch (error) {console.log(error)}
  },

  async updateMeet({dispatch,commit},payload){

    try {

      await firebase.db.collection('meetups').doc(payload.id).update(payload)

      dispatch('fetchMeetups')
  
      const meetup = await firebase.db.collection('meetups').doc(payload.id).get()
  
      commit('setCurrentMeetup', meetup.data())

      router.push('/')
      
    } catch (error) { console.log(error)}
  },

 };

 const mutations = {

  setMeetups(state,val){state.meetups = val;},

  setCurrentMeetup(state,val){ state.currentMeetup = val},

  setLoading(state,val){state.loading = val}
 };

 const getters = {

  meetups: state => state.meetups,
  currentMeetup: state => state.currentMeetup,
  loading: state => state.loading,

};
 
 export default {
    state,
    getters,
    actions,
    mutations
 }