<template>
  <div id="alert" v-show="checkError" >
       <v-alert type="error" height="70px" style="padding: 10px 10px" dense color="purple lighten-1" outlined >
          <span style="font-size : 0.8em" >{{logErrorMess.message}}</span>   
        </v-alert>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'

export default {

    computed:{ ...mapGetters(['logErrorMess']),
    
      checkError(){ return this.logErrorMess.message},
    },
}
</script>

