<template>
  <v-row justify="center" class="mb-5">
      <v-dialog v-model="showEditU" width="560">
          <confirmEditProfil :showDialog="showDialog" @updateAvatar="updateAvatar"  :fieldMessage="fieldMessage" @updateUserProfil="updateUserProfil" @closeConfirm="toggleConfirmEditProfil" @cancelChange="cancelEditUserProfil" />
          <v-form>
               <v-card>
                    <v-card-title><span class="headline">Editer Profile</span></v-card-title>
                    <v-card-text>
                        <v-text-field  @change="toggleConfirmEditProfil(fields.utilisateur)"  type="text" v-model="editCurrentUser.username" color="purple draken-3" clearable label="Nom d'utilisateur"></v-text-field>
                        <v-text-field v-show="!editCurrentUser.SignUpWithFacebook" @change="toggleConfirmEditProfil(fields.email)" type="email" v-model="editCurrentUser.email" color="purple draken-3" clearable label="Email"></v-text-field>
                         <v-file-input accept="image/*" @change="openFileAvatar($event,fields.avatar)"  ref="inputAvatar" color="purple draken-3"  type="file" label="Importer avatar"  prepend-icon="fas fa-user-circle" ></v-file-input>
                        <div style="margin-top:20px">   
                            <p style="text-align:left" class="title">Avatar actuel<span>
                                <v-avatar v-if="!this.avatarUrl && !this.editCurrentUser.avatar" ><v-icon>fas fa-user-circle</v-icon></v-avatar>
                                <v-avatar class="ml-3" v-if="this.avatarUrl"><img :src="avatarUrl"></v-avatar>
                                <v-avatar class="ml-3" v-if="editCurrentUser.avatar"><img :src="editCurrentUser.avatar"></v-avatar></span>
                            </p>
                        </div>
                    </v-card-text>
               <v-card-actions>  
                    <v-btn icon @click="closeShowEdit" ><v-icon color="purple lighten-1">fas fa-window-close</v-icon></v-btn>
                    <v-spacer></v-spacer>
                    <v-btn color="purple darken-1"  @click="cancelEditUserProfil()" text >Annuler</v-btn>
               </v-card-actions>
              </v-card>
            </v-form>
      </v-dialog>
  </v-row>
</template>
<script>

import {mapGetters} from 'vuex'
import confirmEditProfil from '@/components/confirmEditProfil'


export default {

    components:{confirmEditProfil},

    props: ['showEditU'],

    created(){ this.editCurrentUser = {...this.currentUser}},

    computed:{ ...mapGetters(['currentUser'])},

    data(){

        return{

           editCurrentUser: {},
           showDialog: false,
           messageSucces:'succes',

            avatarUrl:'',
            avatarImg:'',
           
           fields: {

            utilisateur: "utilisateur",
            email:'email',
            avatar:'avatar',
           },

           fieldMessage:''
           
        }
    },

    methods:{

        closeShowEdit(){ this.$emit('close')},

        openFileAvatar(input,field){ 
           
          if(input === undefined || input === null){ this.avatarUrl = ""; return}  
            
            let reader = new FileReader();
            reader.readAsDataURL(input);

            this.editCurrentUser.avatar = null;

            reader.addEventListener('load', () =>{

                this.avatarUrl = reader.result;
            })

            this.avatarImg = input
            this.toggleConfirmEditProfil(field)
        },

        updateAvatar(){ 

            this.editCurrentUser.avatarImg = this.avatarImg; 
            this.$store.dispatch('addAvatar',this.editCurrentUser)
        },

        cancelEditUserProfil(){
            
            Object.assign(this.editCurrentUser,this.currentUser);

            if(this.avatarUrl !== ""){this.avatarUrl = "";} this.$refs.inputAvatar.reset()},

        updateUserProfil(){ 

            if(this.editCurrentUser.email !== this.currentUser.email){

                this.$store.dispatch('updateMail',this.editCurrentUser)
                    
            } 

            if( this.editCurrentUser.username !== this.currentUser.username){

                this.$store.dispatch('updateUsername', this.editCurrentUser)
                
            }

        },

        toggleConfirmEditProfil(field){ 

            this.fieldMessage = field
            this.showDialog = !this.showDialog
        },
    },

}

</script>

