<template>
<v-list color="indigo lighten-5">
        <editProfilU v-bind:showEditU="showEditU"  @close="toggleEditU"/>
    <v-list-item-content>
        <v-list-item-title>
            <h4 class="display-2">Profile</h4>
        </v-list-item-title>
    </v-list-item-content>
    
        <v-list-item>
            <v-list-item-content style="text-align:left" >
                 <v-list-item-title class="my-2" ><h3>Nom d'utilisateur :<span style="margin-left:10px" class="title">{{currentUser.username}} </span></h3></v-list-item-title>
                 <v-list-item-title class="my-2" ><h3>Email :<span style="margin-left:10px" class="title">{{currentUser.email}}</span></h3></v-list-item-title>
                 <v-list-item-title class="my-2" v-if="!currentUser.avatar">
                     <h3>Avatar<v-avatar style="margin-left:10px" ><v-icon>fas fa-user-circle</v-icon></v-avatar></h3></v-list-item-title>
                <v-list-item-title class="my-2" v-if="currentUser.avatar">
                    <h3>Avatar<v-avatar style="margin-left:10px" height="40" width="40" ><img :src="currentUser.avatar"></v-avatar></h3></v-list-item-title>
            </v-list-item-content>
        </v-list-item>
      <v-list-item-action >
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
                <v-btn @click="toggleEditU" fab dark x-small color="purple lighten-2">
                    <v-icon  v-on="on" v-bind="attrs">fas fa-pen</v-icon>
                </v-btn>
            </template>
            <span>Editer Profile</span>
          </v-tooltip>
    </v-list-item-action>
</v-list>
 
</template>

<script>
import {mapGetters} from 'vuex'
import editProfilU from '@/components/editProfilU'

export default {

    components:{editProfilU},

    created(){ this.editCurrentUser = {...this.currentUser}},

    computed:{ 

        ...mapGetters(['currentUser'])
    },

    data(){

        return{

            showEditU: false,
           
        }
    },

    methods:{

        toggleEditU(){ this.showEditU = !this.showEditU},
    },

}
</script>

<style>



h4{ color:#6A1B9A}

</style>