<template>
<div id="view">
<v-row>
    <v-col>
      <editMeet v-bind:showEdit="showEdit" @close="toggleEdit" />
  <v-card
    class="mx-auto"
    max-width="750"
  >
    <v-img
      class="white--text align-end"
      height="270px"
      :src= "currentMeetup.imageU"
    >
      <v-card-title>{{currentMeetup.titre}}</v-card-title>
    </v-img>
      <div class="wrapText" >
      <h4 class="mb-5">{{currentMeetup.description}}</h4>
      <p style="font-weight:500"><span>Date : </span>{{currentMeetup.date | date}}</p>
      <p style="font-weight:500" ><span>heure : </span>{{currentMeetup.time}}</p>
      <p style="font-weight:500" ><span>Lieu : </span>{{currentMeetup.lieu}}</p>
      </div>
    <v-card-actions class="mt-4">
      <v-btn @click="toggleShareSocial" class="mr-1"  color="purple draken-1" text>Envoyer une invitation</v-btn>
      <shareSm v-show="showSocialShare" :showSocialShare="showSocialShare" @close='toggleShareSocial'/>
      <v-spacer></v-spacer>
       <v-tooltip bottom >
         <template v-slot:activator="{ on, attrs }">
          <v-btn @click="openSharedMeet(currentMeetup.id)" class="mr-2" fab dark x-small color="purple lighten-2">
            <v-icon  v-on="on" v-bind="attrs">fas fa-users</v-icon>
          </v-btn>
         </template>
         <span>Voir participants</span>
        </v-tooltip> 
       <v-tooltip bottom >
         <template v-slot:activator="{ on, attrs }">
          <v-btn  class="mr-2" fab dark x-small color="purple lighten-2">
            <v-icon  v-on="on" v-bind="attrs" @click="toggleEdit()" >fas fa-pen</v-icon>
           </v-btn>
         </template>
         <span>Editer Meetup</span>
        </v-tooltip>  
         <v-tooltip bottom >
         <template v-slot:activator="{ on, attrs }">
          <v-btn  class="mr-2" fab dark x-small color="purple lighten-2">
            <v-icon  @click="deleteMeet(currentMeetup.id)" v-on="on" v-bind="attrs">fas fa-trash</v-icon>
          </v-btn>
         </template>
         <span>Supprimer meetup</span>
      </v-tooltip>

    </v-card-actions>
  </v-card>
  </v-col>
  </v-row>
  </div>
</template>

<script>


import { mapGetters } from 'vuex';
import shareSm from '@/components/shareSm'
import editMeet from '@/components/editMeet'

export default {

  created: async function(){

    const url = window.location.href

    let index = url.indexOf('viewMeet/')

    let urlId = url.slice(index + 9)

    this.$store.dispatch('loadCurrentMeetup',urlId)

  },

  components:{shareSm,editMeet},

  data(){

    return{

      showEdit: false,
      showSocialShare:false,
    }
  },

  computed: {...mapGetters(['currentMeetup','logUser'])},

  methods: {

    toggleEdit(){ this.showEdit = !this.showEdit },

    deleteMeet(meetupId){ 
      
      this.$store.dispatch('deleteMeet', meetupId)
    
    },

    toggleShareSocial(){ this.showSocialShare = !this.showSocialShare },

    openSharedMeet(id){  this.$router.push('/meetUrl/' + id)}
  }

}
</script>

<style scoped>

#view {

  padding: 10px;
}

.wrapText {

  height: 165px;
  padding:30px;
}

</style>