<template>
<v-container>
  <v-row justify="center">
    <v-col cols="12" md="6">
        <h4>Se connecter via Facebook</h4>
      <v-btn class="mb-2" @click="logFbook" x-large icon><v-icon x-large color="#3b5998">fab fa-facebook-square</v-icon></v-btn>
      <div class="mb-7" style="border-bottom: 2px solid violet"></div>
      <h4 class="mt-10" >Connexion au compte</h4>
      <v-form @submit.prevent="logInUser">
        <v-text-field   color="purple draken-3" @keypress="clearError" v-model.trim ="login.email" label="email" type="email"   clearable ></v-text-field>
        <v-text-field   color="purple draken-3" @keypress="clearError" v-model.trim ="login.password" type="password" label="Password" required  hint="At least 8 characters"   clearable ></v-text-field>
      <v-btn small
        color="purple draken-1 white--text"
        class="mr-4"
        type="submit"
      >
        Valider
      </v-btn>
      </v-form>
         <v-btn style="text-transform:lowercase" small text @click="onClickResetPassword(login.email)" >Mot de passe oublié ?</v-btn>
       <v-alert v-show="resetPassword" color="purple lighten-1 white--text"
       >Un mail vous a été envoyé afin de procéder au changement de mot de passe.Veuillez consulter votre messagerie
       </v-alert>
      <v-row justify-md="center">
      <v-col md="12"  style="margin-top: 20px">
       <alertError/>
      </v-col>
      </v-row>
    </v-col>
  </v-row>
</v-container>
</template>

<script>

import alertError from '@/components/alertError'
import * as firebase from '../../Firebase/firebase'

export default {

  components:{

    alertError
  },

  data(){

    return{

      login:{
        email:'',
        password:'',
      },

      resetPassword:false,
    }
  },

 
  methods: { 

    logInUser(){ 
      this.$store.dispatch('loginUser', this.login)
      this.clearError()
    },

   async logFbook(){

     try {

      await firebase.auth.signInWithPopup(firebase.provider)
      this.$router.push('/')
       
     } catch (error) { console.log(error)}
   },

    signoutFbook(){ this.$store.dispatch('signoutUser')},

    clearError(){ this.$store.commit('setClearError')},

   async onClickResetPassword(email){ 
      
    try {

      await firebase.auth.sendPasswordResetEmail(email)
      this.resetPassword = true;

    } catch (error) { console.log(error) }
     
  },
  }

}
</script>

