<template>
  <v-row justify="center">
    <v-dialog v-model="showEdit" max-width="600px">
      <v-form @submit.prevent="onSubmitEditMeet" >
      <v-card>
        <v-card-title>
          <span class="headline">Editer Meetup</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field color="purple draken-1" v-model ="editedMeet.titre" clearable dense label="Titre" required></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field color="purple draken-1" v-model="editedMeet.lieu" clearable dense label="Lieu" required></v-text-field>
              </v-col>
              <v-col cols="12">
                    <v-menu
                        ref="menu"
                        v-model="menu"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        min-width="290px"
                    >
                        <template v-slot:activator="{ on, attrs }">
                            <v-text-field
                                color="purple draken-1"
                                v-model="editedMeet.date"
                                label="Date"
                                readonly
                                v-bind="attrs"
                                v-on="on"
                            ></v-text-field>
                        </template>
                            <v-date-picker locale="fr" v-model="editedMeet.date" no-title scrollable>
                                <v-spacer></v-spacer> 
                                <v-btn text color="primary" @click="menu = false">Annuler</v-btn>
                                <v-btn text color="primary" @click="pickDate($event)" :data-date="editedMeet.date">OK</v-btn>
                            </v-date-picker>
                        </v-menu>
                    </v-col>
              <v-col cols="12">
                  <v-menu
                    ref="menu"
                    v-model="menu2"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    transition="scale-transition"
                    offset-y
                    max-width="290px"
                    min-width="290px"
                >
                <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                        color="purple draken-1"
                        v-model="editedMeet.time"
                        label="Heure"
                        v-bind="attrs"
                        readonly
                        v-on="on"
                        format="24hr"
                    ></v-text-field>
                </template>
                <v-time-picker
                locale='fr'
                format="24hr"
                v-if="menu2"
                v-model="editedMeet.time"
                full-width
                @click:minute="menu2 = false"
                @change="pickTime($event)"
                ></v-time-picker>
                </v-menu>
              </v-col>
              <v-col cols="12">
                <v-text-field color="purple draken-1" v-model="editedMeet.description" clearable dense label="Description" required></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-btn icon>
            <v-icon color="purple lighten-1" @click="cancelEdit" >fas fa-window-close</v-icon>
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="purple darken-1"  @click="cancelEdit" text >Annuler</v-btn>
          <v-btn type="submit" color="purple ligthen-2 white--text" >Enregister</v-btn>
        </v-card-actions>
      </v-card>
      </v-form>
    </v-dialog>
  </v-row>
</template>
<script>

import {mapGetters} from 'vuex';

  export default {

    props: ['showEdit'],

    created(){ this.editedMeet = {...this.currentMeetup}},

    computed : {...mapGetters(['currentMeetup']),

    },
      
    data(){

    return{

      menu2: false,
      modal2: false,
      menu: false,
      modal: false,

      editedMeet:{}
    } 
    },

    methods: {

      onSubmitEditMeet(){ this.$store.dispatch('updateMeet',this.editedMeet)},

      pickTime(event){ this.editedMeet.time = event;},

      pickDate(event){ 
        
        this.editedMeet.date = event.target.parentNode.dataset.date; 

        this.menu = false
      },

     cancelEdit(){ 

      this.editedMeet = {...this.currentMeetup}
      this.$emit('close')
    },

    }
  }

</script>