 <template>
  <v-row justify="center" style="margin-top: 100px">
    <v-dialog
      v-model="dialog"
      max-width="330"
    >
      <v-card >
        <v-col>
        <v-card-title class="ml-16">Se déconnecter ?</v-card-title>
        <v-card-actions>
          <v-btn  
            color="purple darken-1"
            text
            @click="signoutUser"
          >
            Déconnexion
          </v-btn>
          <v-btn  
            color="purple darken-1"
            text
            style="margin-left:70px"  
            @click="annulDec"
          >
            Annuler
          </v-btn>
        </v-card-actions>
        </v-col>
      </v-card>
    </v-dialog>
  </v-row>
</template>


<script>
  export default {
    data () {
      return {

        dialog: true,
      }
    },

    methods:{

      signoutUser(){ 
        
        this.$store.dispatch('signoutUser')
      },

      annulDec(){  this.dialog = false; this.$router.push('/') },
      
    }
  }
</script>

